body{
  overflow-x: hidden !important;
}
p {
  font-size: 16px !important;
  font-family: 'Roboto', sans-serif;
}

h1,
h2,
h3,
h3 {
  font-family: 'Phudu', cursive;
  font-weight: bold !important;

}

.navbar {
  background-color: white !important;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;
}

.navbar h3,
.navbar a {
  color: black !important;
}

.navbar ul li {
  margin: 0px 15px !important;
  font-weight: 500;
}

.navbar a::after {
  content: '';
  width: 0%;
  height: 2px;
  display: block;
  backgrosund-color: #1e5186;
  transition: width 0.4s ease-in-out;
}

.navbar a:hover::after {
  width: 100%;
}

.hero-section {
  background-color: white;
  height: auto !important;
  min-height: 100vh !important;
  width: 100%;
  position: relative !important;
}

.primary-btn {
  background-color: #1e5186 !important;
  padding: 10px !important;
  border: 1px solid #1e5186;
  outline: none;
  color: white;
  font-weight: 500;
  transition: all 0.5s ease;
  border-radius: 4px;
}

.primary-btn:hover {
  background-color: transparent !important;
  color: #1e5186;
}

.secondary-btn {
  background-color: transparent !important;
  padding: 10px !important;
  border: 1px solid #1e5186;
  outline: none;
  color: #1e5186;
  margin-left: 10px !important;
  font-weight: 500;
  transition: all 0.4s ease;
  font-weight: 500;
  border-radius: 4px;
}

.secondary-btn:hover {
  background-color: #1e5186 !important;
  color: white;
}

.hero-img {
  max-width: 429px !important;
}

.main-heading {
  font-size: 60px;
  color: #1e5186;
  font-weight: bold;
}

.social-links {
  position: absolute !important;
  left: 0;
  border: 1px solid #1e5186;
  bottom: 140px !important;
  background-color: #1e5186;
  padding: 10px !important;
  margin: 0px !important;
  display: flex;
  align-items: center !important;
  border-top-right-radius: 25px !important;
  flex-direction: column !important;
}

.social-links .circle {
  width: 40px;
  height: 40px;
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin: 10px 0px !important;
}

.circle a {
  color: white;
}

.circle a i {
  font-size: 20px !important;
}

/* about */
.about {
  width: 100%;
  height: auto !important;
  min-height: 100vh !important;
  margin-top: 100px !important;
}

.info-heading {
  color: #1e5186;
  text-align: center;
  text-transform: uppercase;
}


.about img {
  max-width: 450px;
}

/* .contact-me */


.contact-box {
  width: 90%;
  margin-bottom: 10px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  padding: 20px !important;
  border-radius: 8px;
}

.contact-me input {
  padding: 15px !important;
  border: 1px solid #1e5186;
  background-color: transparent;
  outline: none;
  color: #1e5186;
  font-size: 18px;
  border-radius: 2px;
  width: 95%;

}



.form-control:focus {
  color: #1e5186;
  background-color: transparent;
  border-color: #1e5186;
  outline: 0;
  box-shadow: none;
}

.contact-me .textarea {
  border: 1px solid #1e5186;
  background-color: transparent;
  border-radius: 2px;
  padding: 15px;
  color: #1e5186;
  outline: none;
}



/* skills */
.skills {
  height: auto !important;
  min-height: 100vh !important;
}

.skills .box {
  width: calc(100% / 5 - 30px);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  padding: 20px !important;
  text-align: center;
  transition: all 0.4s ease;
  border: 1px solid white;

}

.skills .box i {
  font-size: 50px;
  margin-bottom: 20px;
  transition: color 0.4s ease;
}

.skills .box:hover {
  transform: scale(1.04);
  border: 1px solid #1e5186;
}

.skills .box:hover i {
  color: #1e5186;
}

.about .about-box {
  width: calc(100% / 3 - 20px);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 15px;
  border-radius: 10px;
  text-align: center;
  transition: all 0.4s ease;
  border: 1px solid white;
}

.about .about-box i {
  font-size: 30px;
  margin-bottom: 20px;
  transition: color 0.4s ease;
}

.about .about-box:hover i {
  color: #1e5186;
}

.about .about-box:hover {
  transform: scale(1.04);
  border: 1px solid #1e5186;
}


/* animation */
.mouse-pointer-container {
  position: fixed;
  pointer-events: none;
  z-index: 9999;
}

.custom-cursor {
  width: 10px;
  height: 10px;
  background-color: #1e5186;
  opacity: 0.4;
  /* Customize the color */
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  transform: translate(-50%, -50%);
  transition: transform 0.15s ease-in-out, background-color 0.3s ease-in-out;
}

/* Add a hover effect */
.custom-cursor:hover {
  background-color: green !important;
  /* Customize the hover color */
  transform: translate(-50%, -50%) scale(1.2);
  /* Add a scale effect on hover */
}

.custom-cursor,
.custom-cursor-small {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  opacity: 0.4;
  position: fixed;
  pointer-events: none;
  transform: translate(-50%, -50%);
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.custom-cursor {
  background-color: #1e5186;
  /* Customize the color for the larger circle */
  animation-name: bubble;
}

.custom-cursor-small {
  background-color: #1e5186;
  /* Customize the color for the smaller circle */
  animation-name: bubble-small;
}

@keyframes bubble {

  0%,
  100% {
    transform: translate(-50%, -50%) scale(1);
  }

  50% {
    transform: translate(-50%, -50%) scale(1.2);
  }
}

@keyframes bubble-small {

  0%,
  100% {
    transform: translate(-50%, -50%) scale(0.7);
  }

  50% {
    transform: translate(-50%, -50%) scale(0.9);
  }
}

.contact-me i {
  font-size: 20px;
  margin-right: 20px !important;

}

